<template>
 <div>
  <h2>  付款頁面</h2>

   {{ order }}
      <form action="https://ccore.spgateway.com/MPG/mpg_gateway" method="post">
        <input type="text" name="MerchantID" value="MS138942195" />
        <input type="text" name="TradeSha" :value="order.shaEncrypt" />
        <input
          type="text"
          name="TradeInfo"
          :value="order.aesEncrypt"
        />
        <input type="text" name="TimeStamp" :value="order.order.timeStamp" />
        <input type="text" name="Version" value="1.5" />
        <input type="text" name="MerchantOrderNo" :value="order.order.merchantOrderNo" />
        <input type="text" name="Amt" :value="order.order.amt" />
        <input type="email" name="Email" :value="order.order.email" />
        <button type="submit">確認付款</button>
      </form>
      </div>
</template>

<script>

export default {
	name: 'OrderPayment',
	data() {
		return {
			orderId: null,
			order: {}
		};
	},

	created() {

	},
	async mounted() {
		try {
			this.isLoading = true;
  		this.orderId = this.$route.params.id;

			const resData = await this.$api.getPaymentInfo(this.orderId);
			console.log('resData', resData);
    	if (!resData.data || resData.status !== 'success') {
				throw new Error('取得付款資訊失敗！');
			}

			this.order = resData.data.order;
		} catch (error) {
  	this.$toasted.show(error.message);
			this.isLoading = false;
		}
	},
	methods: {

	}
};
</script>

